import Bmob from "hydrogen-js-sdk";
import loading from "@/components/Loading/index.vue"
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"

let that;
let user;
let uid;
export default {
	components: {
		loading,
		faIcon,
		uniPopup
	},
	data() {
		return {
			is_choose:false,
			loading: true,
			frist_class: '', //一级分类
			M_second_class: '', //二级分类
			selected_id: '', //选择的id
			class_text: '', //分类的输入内容
			input_class_text: '', //已经输入的内容
			middle: false,
			popup_editshow: false,
			Popup_title: "一级分类",
		}
	},
	onLoad(options) {
		that = this;
		user = uni.getStorageSync("user");
		uid = uni.getStorageSync("uid");
		
		if(options.type =="choose"){
			that.is_choose = true
		}
	},
	onShow() {
		that.get_category();
	},
	methods: {
		
		//选择分类的情况下选择分类
		select_this(item,type){
			item.type = type
			uni.setStorageSync("category", item)
			uni.navigateBack({
				delta: 1
			})
		},
		
		//得到一级分类
		get_category() {
			const query = Bmob.Query("M_frist_class");
			query.equalTo("parent", "==", uid);
			query.find().then(res => {
				console.log(res)
				that.frist_class = res;
				
				if(res.length == 0){
					that.loading = false;
				}else{
					that.get_second_category(res[0].objectId)
				}

				
			});
		},

		//得到二级分类
		get_second_category(id) {
			that.selected_id = id;
			const query = Bmob.Query('M_frist_class')
			query.field('second', id)
			query.relation('M_second_class').then(res => {
				console.log(res);
				that.loading = false;
				that.M_second_class = res.results;
			})
		},

		//点击编辑图标
		showoption(id, type, value) {
			console.log(id, type, value)
			uni.showActionSheet({
				itemList: ['删除', '编辑'],
				success: function(res) {
					console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					if (res.tapIndex == 0) {
						if (type === 1) {
							that.delete(id, "M_frist_class")
						} else {
							that.delete(id, "M_second_class")
						}

					} else {
						that.input_class_text = value
						that.selected_id = id
						if (type === 1) {
							that.Popup_title = "一级分类"
							that.popup_editshow = true;
						} else {
							that.Popup_title = "二级分类"
							that.popup_editshow = true;
						}
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		//点击一级分类事件
		add_fristclass() {
			this.Popup_title = "一级分类";
			that.middle = true;
		},

		//点击一级分类事件
		add_secondclass() {
			this.Popup_title = "二级分类";
			that.middle = true;
		},

		hidePopup() {
			this.middle = false;
			this.popup_editshow = false;
		},

		//编辑分类内容
		edit() {
			let dbname;
			if (that.Popup_title === "一级分类") {
				dbname = "M_frist_class";
			} else {
				dbname = "M_second_class";
			}
			console.log(that.selected_id, dbname)
			const query = Bmob.Query(dbname);
			query.set('id', that.selected_id); //需要修改的objectId
			query.set('class_text', that.input_class_text);
			query.save().then(res => {
				console.log(res);
				uni.showToast({
					title: '修改成功'
				});
				that.get_category();
				
				that.middle = false;
				that.popup_editshow = false;
			}).catch(err => {
				console.log(err)
			})
		},

		//删除分类
		delete(id, dbname) {
			uni.showModal({
				title: '提示',
				content: '是否删除此分类',
				success(res) {
					if (res.confirm) {
						const query = Bmob.Query(dbname);
						query.destroy(id).then(res => {
							console.log(res)
							uni.showToast({
								title: '删除成功',
								duration: 1000
							});
							that.get_category();
						}).catch(err => {
							console.log(err)
						})
					}
				}
			})
		},

		//popup确定点击
		confrim() {
			const class_text = that.class_text;

			if (this.Popup_title === "一级分类") {
				if (class_text == '') {
					uni.showToast({
						icon: "none",
						title: '请输入类别',
					})
				} else {
					const pointer = Bmob.Pointer('_User')
					const poiID = pointer.set(uid);

					const query = Bmob.Query('M_frist_class');
					query.set("parent", poiID)
					query.set("class_text", class_text);
					query.save().then(res => {
						that.get_category();
						this.middle = false;
						this.class_text = '';
					}).catch(err => {
						console.log(err)
					})
				}
			} else {
				//console.log(that.selected_id)
				const selected_id = that.selected_id

				const pointer = Bmob.Pointer('M_frist_class')
				const poiID = pointer.set(selected_id)

				const query = Bmob.Query('M_second_class');
				query.set("class_text", class_text)
				query.set("parent", poiID)
				query.save().then(res => {
					//console.log(res)
					const relation = Bmob.Relation('M_frist_class') // 需要关联的表
					const relID = relation.add([res.objectId]) //关联表中需要关联的objectId, 返回一个Relation对象, add方法接受string和array的类型参数
					const query = Bmob.Query('M_frist_class')
					query.get(selected_id).then(res => {
						res.set('second', relID); // 将Relation对象保存到two字段中，即实现了一对多的关联
						res.save()

						that.get_second_category(selected_id)
						this.middle = false;
						this.class_text = '';
					})
				}).catch(err => {
					console.log(err)
				})

			}

		},

	}
}
